import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
// import { ref } from 'vue'
export default {
  name: 'MyComponent',
  components: { VueSlickCarousel },
  data () {
    return {
      settings: {
        arrows: true,
        dots: true,
        responsive: [{
          breakpoint: 9999,
          settings: 'unslick'
        },
        {
          breakpoint: 767,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            centerMode: true,
            infinite: true,
            arrows: false,
            dots: true
          }
        }]
      }
    }
  }
}
